<template>
	<view class="commonFooter" :style="{height: height + 'px'}">
		<view class="commonFooter-content" :style="{height: height + 'px'}"></view>
		<view class="commonFooter-fixed" :style="{height: height + 'px'}"></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				height: 0
			}
		},
		created() {
			this.height = this.$store.state.isIphoneX? 44 : 0
		}
	}
</script>

<style lang="less" scoped>
	.commonFooter{
		width: 100%;
		&-content{
			width: 100%;
		}
		&-fixed{
			width: 100%;
			position: fixed;
			bottom: 0;
		}
	}
</style>
